import { SheetOptions } from '@fui/sheet/types';
import { ResizeOption } from './resizer';
import { AbstractResizer } from './resizer.abstract';
import { adjustSize, removeColsAdjust, removeRowsAdjust } from './resizer.service';

/**
 * 不调整 + 移除报表间距
 */
export class NoneTableResizer extends AbstractResizer {
    public isAdaptive = false;
    
    public resize(sheet: SheetOptions, opt: ResizeOption): SheetOptions {
        const { tableWidth, tableHeight } = opt;
        const { rows = [], cols = [] } = sheet.sheet;
        adjustSize(tableHeight, removeRowsAdjust(rows));
        adjustSize(tableWidth, removeColsAdjust(cols));
        
        return sheet;
    }
}
